<template>
	<div>
		<TopProducts />
		<p class="text-center" v-if="loading"><span class="spinner"></span></p>
		<div class="products-container" v-if="products.length > 0">
			<Product v-for="(product, index) in products" :key="index" :product="product" />
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Home',
		components: {
			TopProducts: () => import(/* webpackChunkName: "TopProducts" */ '@/components/TopProducts.vue'),
			Product: () => import(/* webpackChunkName: "Product" */ '@/components/Product.vue')
		},
		data: () => ({
			loading: true,
			products: []
		}),
		mounted(){
			setTimeout(() => {
				this.products = this.$store.state.products
				this.loading = false
			}, 1000)
		}
	}
</script>

<style>
.products-container{
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: 350px;
	grid-gap: 20px;
}
</style>