<template>
<form method="post" action="/placeorder" @submit.prevent="submitOrder()">
	<div class="order-page">
		<div class="form mb-2">
			<h1 class="mb-2">Personal Info</h1>
			<label>Address 1</label>
			<input type="text" class="input" placeholder="Address 1">
			<label>Address 2</label>
			<input type="text" class="input" placeholder="Address 2">
			<label>Email</label>
			<input type="text" class="input" placeholder="Email address">
			<label>Zip</label>
			<input type="text" class="input" placeholder="Zip code">
		</div>
		<div class="form card-info">
			<h1 class="mb-2">Card Info</h1>
			<label>Payment method</label>
			<div class="radio-input">
				<input type="radio" name="pmethod" checked>
				<label>MasterCard</label>
			</div>
			<div class="radio-input">
				<input type="radio" name="pmethod">
				<label>PayPal</label>
			</div>
			<label>Name</label>
			<input type="text" class="input" placeholder="Name on card">
			<label>Card number</label>
			<input type="text" class="input" placeholder="Card number">
			<label>Expiry date</label>
			<input type="text" class="input" placeholder="Expiry date">
		</div>
	</div>
	<div class="radio-input">
		<input type="checkbox" v-model="agreeTerms">
		<label>I agree with the terms and conditions.</label>
	</div>
	<p class="text-center mt-1">
		<button class="btn" type="submit">submit order</button>
	</p>
</form>
</template>

<script>
	import { toast } from '@/utils'
	export default{
		name: 'Order',
		data: () => ({
			agreeTerms: false
		}),
		methods: {
			submitOrder(){
				const cart = this.$store.state.cart
				if(!this.agreeTerms){
					toast('your must agree with our terms and conditions', 'err')
					return
				}
				if(cart.length < 1){
					toast('your cart is empty', 'err')
					return
				}
				let date = (new Date()).toString().split(" ").slice(0, 3).join(" ")
				let code = ""
				for(let i = 0; i < 8; i++){
					code += parseInt(Math.random() * 8) + 1
				}
				let order = {
					time: date,
					orders: cart,
					code
				}
				this.$store.state.orders.unshift(order)
				this.$store.state.cart = []
				toast('Order completed!')
				this.$router.push({
					path: '/orders'
				})
			}
		}
	}
</script>

<style>
.order-page{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.order-page .form{
	width: calc(50% - 15px);
}
.order-page .form .input{
	margin-bottom: 10px;
}
.order-page .form .radio-input{
	padding-left: 10px;
}
</style>