<template>
<div>
	<p class="text-center" v-if="loading"><span class="spinner"></span></p>
	<div v-else-if="orders.length > 0">
		<div class="mb-4" v-for="(order, index) in orders" :key="index">
			<h1 class="mb-2">{{ order.time }} ({{ order.code }})</h1>
			<CartItem v-for="(item, index3) in order.orders" :key="index2" :item="item" :hideNav="true" />
		</div>
	</div>
	<h1 class="text-center h1-msg grey-text" v-else>No order placed yet!</h1>
</div>
</template>

<script>
	export default{
		name: 'Orders',
		components: {
			CartItem: () => import(/* webpackChunkName: "CartItem" */ '@/components/CartItem.vue')
		},
		data: () => ({
			loading: true,
			orders: []
		}),
		mounted(){
			setTimeout(() => {
				this.orders = this.$store.state.orders
				this.loading = false
			}, 350)
		}
	}
</script>