<template>
	<div class="slider-wrapper">
		<div class="slider" :style="{width: (100 * len) + '%', left: -(current * (100 )) + '%'}">
			<img v-for="(img, index) in slides" :key="index" :src="img" :style="{width: (100 / len) + '%'}">
		</div>
		<div class="slider-nav">
			<i class="material-icons" @click="prev()">keyboard_arrow_left</i>
			<i class="material-icons" @click="next()">keyboard_arrow_right</i>
		</div>
	</div>
</template>

<script>
	export default{
		name: 'ImageSlider',
		props: ['slides'],
		computed: {
			len(){
				return this.slides.length
			}
		},
		data: () => ({
			current: 0
		}),
		methods: {
			next(){
				if(this.current < this.len - 1){
					this.current++
				}else{
					this.current = 0;
				}
			},
			prev(){
				if(this.current > 0){
					this.current--
				}else{
					this.current = this.len - 1
				}
			}
		}
	}
</script>

<style>
.slider-wrapper{
	width: 100%;
	height: 25vw;
	overflow: hidden;
	position: relative;
}
.slider-wrapper .slider{
	height: 100%;
	position: absolute;
	top: 0;
	transition: left 0.5s;
}
.slider img{
	height: 100%;
}
.slider-wrapper .slider-nav{
	position: absolute;
	bottom: 0;
	left: 0;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.3);
	opacity: 0.6;
	transition: opacity 0.2s;
}
.slider-nav:hover{
	opacity: 1;
}
.slider-nav i{
	font-size: 30px;
	opacity: 0.6;
	color: #fff;
	transition: opacity 0.2s;
}
.slider-nav i:hover{
	opacity: 1;
}
</style>