<template>
<div>
	<p class="text-center" v-if="loading"><span class="spinner"></span></p>
	<div class="order" v-else-if="cart.length > 0">
		<div class="cart">
			<CartItem v-for="(item, index) in cart" :key="index" :item="item" @removeFromCart="removeFromCart(index)" />
		</div>
		<div class="form">
			<h1><b>Total:</b> <span>$</span>{{ total.toFixed(2) }}</h1>
			<h1><b>VAT:</b> {{ vat }}%</h1>
			<h1><b>Delivery charge:</b> <span>$</span>{{ deliveryCharge }}</h1>
			
			<label class="mt-2">Got a coupon?</label>
			<input type="text" class="input mb-2" placeholder="Enter coupon code">
			<h1><b>Grand total:</b> <span>$</span>{{ grandTotal.toFixed(2) }}</h1>
			<p class="mt-2"><router-link to="/order" class="btn">place order</router-link></p>
		</div>
	</div>
	<h1 class="text-center h1-msg grey-text" v-else>Cart is empty!</h1>
</div>
</template>

<script>
	export default{
		name: 'Cart',
		components: {
			CartItem: () => import(/* webpackChunkName: "CartItem" */ '@/components/CartItem.vue')
		},
		data: () => ({
			loading: true,
			cart: [],
			vat: 5,
			deliveryCharge: 10
		}),
		computed: {
			total(){
				let t = 0
				this.cart.map(e => t += (e.price *e.qty))
				return t
			},
			grandTotal(){
				return this.total + (this.total * (this.vat / 100)) + this.deliveryCharge
			}
		},
		mounted(){
			setTimeout(() => {
				let cart = this.$store.state.cart
				this.cart = cart
				this.loading = false
			}, 350)
		},
		methods: {
			removeFromCart(i){
				this.cart.splice(i, 1)
			}
		}
	}
</script>

<style>
.order{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.order .cart, .order .form{
	width: calc(50% - 15px);
}
.order .form h1{
	font-weight: 100;
	color: #1D1D1D;
	margin-bottom: 5px;
}
.order .form h1 span{
	color: var(--primary-color);
}
.order .cart .cart-item:last-child{
	margin-bottom: 0;
}
.order .form{
	align-self: flex-end;
}
.order .form input{
	width: 200px;
}
</style>